<template>
    <div class="block">
    <button @click="stop">停止</button>
    <button @click="start">游戏开始</button>
    <div style="color: red; text-align: center;font-size: 25px">score:{{scroce}}</div>
    <div class="box" :style="{width :boxWidth +'px', height:boxHeight +'px'}">
        <div class="str">{{str}}</div>
        <div class="kuaiBox">
            <div class="kuai" v-for="(item,index) in arr" :key="index" :style="{opacity :item.active ? '0':'1'}"></div>
        </div>
        <div class="ball" :style="{left :x + 'px', top : y + 'px', width : ball +'px', height: ball+'px'}"></div>
        <div class="bottomMove"
             :style="{left :mx + 'px' , top : my + 'px',width :moveBottomW +'px',height : moveBottomH+'px'  }"></div>
    </div>

    </div>
</template>
<script >
    let timer = null, // 小球定时器
        speed = 3,// 小球速度
        map = {x: 10, y: 10},
        timer2 = null, // 挑战成功字眼显示定时器
        index = 0//挑战成功字眼续个显示的索引值
    export default {
        name : 'block',
        data(){
            return{
                ball : 10,
                boxWidth : 800, // 场景宽度
                boxHeight : 300, // 场景高度
                moveBottomH : 5,//移动方块高度
                moveBottomW : 100,//移动方块快读
                x: 500 / 2 - 10 / 2,  // 小球x轴位置信息 计算默认位置在中间
                y: 300 - 10 - 5, // 小球Y轴的位置信息 计算默认位置在中间
                mx: 500 / 2 - 100 / 2, //移动方块的位置信息 计算默认位置在中间
                my: 300 - 5, // 移动方块y轴的的位置信息  计算默认位置在中间
                // 被打击方块的数组
                arr: Array.from({length: 50}, (_, index) => {
                    return {
                        index,
                        active: false
                    }
                }),
                str: '', // 返回挑战成功字眼
                scroce: 0 // 分数
            }
        },
        mounted() {
            document.addEventListener('keyup', this.bottomMove.bind(this))
        },
        destroyed() {
            clearInterval(timer)
        },
        methods : {
            stop(){
                clearInterval(timer)
            },
            strFun(){
                if (strArr.length === index) clearInterval(timer2)
                this.str += strArr.substr(index, 1)
                index++
            },
            moveBall(){
                const {offsetTop, offsetHeight, offsetLeft, offsetWidth} = document.querySelector('.bottomMove')
                if (this.x <= 0) {
                    map.x = speed
                } else if (this.x > this.boxWidth - this.ball) {
                    map.x = -speed
                }
                if (this.y <= 0) {
                    map.y = speed
                }
                if (this.y >= offsetTop - offsetHeight &&
                    this.y <= offsetTop + offsetHeight &&
                    this.x >= offsetLeft &&
                    this.x < offsetLeft + offsetWidth) {
                    map.y = -speed
                }
                if (this.y > this.boxHeight) {
                    clearInterval(timer)
                    alert('game over')
                    window.location.reload()
                }
                Array.from(this.arr).forEach((item, index) => {
                    const {
                        offsetLeft,
                        offsetTop,
                        offsetWidth,
                        offsetHeight
                    } = document.querySelectorAll('.kuai')[index]
                    if (this.x > offsetLeft
                        && this.x < offsetLeft + offsetWidth
                        && this.y > offsetTop
                        && this.y < offsetTop + offsetHeight) {
                        if (!this.arr[index].active) {
                            this.scroce += 100
                        }
                        this.arr[index].active = true
                    }
                })
                if (Array.from(this.arr).every(item => item.active)) {
                    clearInterval(timer)
                    timer2 = setInterval(this.strFun.bind(this), 1000)
                }
                this.x = this.x += map.x
                this.y = this.y += map.y
            },
            bottomMove(ev){
                if (ev.code === 'Space') clearInterval(timer)
                switch (ev.key) {
                    case 'ArrowRight':
                        this.mx += 100
                        break
                    case  'ArrowLeft':
                        this.mx -= 100
                        break
                }
                this.mx = this.mx < 0 ? 0 : this.mx
                this.mx = this.mx > this.boxWidth - this.moveBottomW ? this.boxWidth - this.moveBottomW : this.mx
            },
            start(){
                timer = setInterval(this.moveBall.bind(this), 20)

            }
        }
    }


    const strArr = '恭喜你,挑战成功!!'

</script>

<style>

    .bottomMove {
        width: 100px;
        height: 10px;
        background: red;
        position: absolute;
        transition-duration: 100ms;
        transition-timing-function: ease-out;
    }

    .ball {
        width: 20px;
        height: 20px;
        background-color: red;
        border-radius: 50%;
        position: absolute;
    }

    .kuaiBox {
        display: flex;
        flex-wrap: wrap;
    }

    .kuai {
        width: 30px;
        height: 10px;
        background: red;
        margin: 10px;
        transition-duration: 100ms;
        transition-timing-function: ease-in;
    }

    .str {
        text-align: center;
        font-size: 50px;
        color: red;

    }

    .box {

        justify-content: center;
        width: 500px;
        height: 500px;
        margin: 0 auto;
        position: relative;
        border: 5px solid red;
        overflow: hidden;
    }

    .picker {
        width: 50px;
        height: 50px;
    }
</style>
